a para regar cultivos que se encuentren cerca al río y consiste en realizar una abertura en el cauce natural y dirigiendo el agua por medio de un sistema de conducción, que la mayoría de veces suele ser un canal.")])])]),_c('div',{staticClass:"row py-5 justify-content-center align-items-center",attrs:{"titulo":"Solera"}},[_c('div',{staticClass:"col-md-5 mb-4 mb-md-0"},[_c('p',[_vm._v("El éxito de esta toma radica en lograr la captación de la parte inferior de un escurrimiento donde las condiciones naturales de flujo se alteran mediante una cámara transversal de captación.")]),_c('p',{staticClass:"mb-4"},[_vm._v("Esta obra se emplaza a manera de travesaño de fondo, sobre la cámara de captación se emplaza una rejilla, con el fin de que habilite el ingreso de caudal de captación y limite el ingreso de sedimentos.")])]),_c('div',{staticClass:"col-md-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/acor2.svg"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row py-5 justify-content-center align-items-center",attrs:{"titulo":"Caudal de una fuente y dotación"}},[_c('div',{staticClass:"col-md-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/t1/acor3.svg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-md-5 mb-4 mb-md-0"},[_c('p',[_vm._v("Al hablar de caudal se refiere a la cantidad de agua que suministra una fuente hídrica durante una unidad de tiempo y se determina a través de aforos, es una técnica que consiste en medir la cantidad de agua que circula en un tiempo determinado y su unidad de medida es litros por segundos (l/s).")]),_c('p',{staticClass:"mb-4"},[_vm._v("La dotación es simplemente la cantidad de agua que una persona tiene derecho para realizar sus actividades diarias. La capacidad de una fuente hídrica para suministrar agua a una comunidad se puede estimar de la siguiente forma, calculando la dotación y multiplicando por el número de personas que residen ahí.")]),_c('u